import React, { Component, PropTypes } from 'react';
import { Motion, spring } from 'react-motion';
import c from 'classnames';
import './RevealText.css';

export default class RevealText extends Component {
  static propTypes = {
    text: PropTypes.oneOfType([
      PropTypes.element,
      PropTypes.string
    ]).isRequired,
    coverClassName: PropTypes.string
  };

  constructor(props) {
    super(props);
    this.state = {
      blockStyle: {
        width: 0,
        x: 0
      },
      textStyle: {
        opacity: 0
      }
    };
  }

  componentDidMount() {
    this.setState({
      blockStyle: {
        width: spring(100),
        x: 0
      },
      textStyle: {
        opacity: 0
      }
    });

    // Run this after .5 second
    setTimeout(() => {
      this.setState({
        blockStyle: {
          width: spring(0),
          x: spring(this.container.offsetWidth)
        },
        textStyle: {
          opacity: 1
        }
      });
    }, 500);
  }

  render() {
    const { blockStyle, textStyle } = this.state;
    const { text, coverClassName, ...others } = this.props;
    const textElemGen = typeof text === 'string' ?
      (textStyle) => <h1 style={textStyle}>{text}</h1> :
      (textStyle) => React.cloneElement(text, { style: textStyle });
    return (
      <div className={'revealText'} {...others}>
        <div className={'revealText__container'} ref={(elem) => this.container = elem}>
          <Motion style={blockStyle}>
            {({ width, x }) => <div className={c('revealText__block', coverClassName)} style={{
              width: `${width}%`,
              transform: `translateX(${x}px)`
            }}></div>}
          </Motion>
          <Motion style={textStyle}>
            {textElemGen}
          </Motion>
        </div>
      </div>
    );
  }
}



